	// Save Workflow Result (8月26日) 保存分析结果
	postSaveResultModels(projectId, data, workflowId) {
		return HTTP.post(`/results/${workflowId}?projectId=${projectId}`, data);
	},

	// 分析工具classiify的请求
	getModelModels() {
		return HTTP.get(`/models`);
	},
	/****  workflow相关接口结束  ****/

	/****  result相关接口  ****/
	// Get Project Results (8月27日) 获取每个项目的分析结果
	getProjectResultsModels(projectId) {
		return HTTP.get(`/results?projectId=${projectId}`);
	},

	//Get saved Result Info (8月27日) 获取单个项目分析结果所有节点信息
	getResultInfoModels(projectId, workflowId, resultId) {
		return HTTP.get(
			`/results/${resultId}?projectId=${projectId}&workflowId=${workflowId}`,
		);
	},

	// Delete Result (8月27日) 删除分析结果
	deleteResultModels(projectId, resultId) {
		return HTTP.delete(`/results/${resultId}?projectId=${projectId}`);
	},

	saveResultModels(projectId, data, workflowId) {
		return HTTP.post(`/results/${workflowId}?projectId=${projectId}`, data);
	},
	/****  result相关接口结束  ****/
	/**2022/2/27 **/
	downloadResult(workflowId, projectId) {
		return HTTP.get(
			`/workflows/${workflowId}/download?projectId=${projectId}`,
			"download",
		);
	},

	/* library相关接口 */
	/**
	 * 2022/3/1/
	 *
	 */
	//拿到所有的文件夹
	allLibrary() {
		return HTTP.get("/library");
	},
	//查看文件夹下的文件
	getFolder(id) {
		return HTTP.get("/library/" + id);
	},
	//查看文件里的内容
	getFolderContent(id, name, params) {
		return HTTP.get(`/library/${id}/${name}`, params);
	},
	//新建文件夹
	createFolder(data) {
		return HTTP.post(`/library`, JSON.stringify(data));
	},
	uploadFile(id, formData) {
		return HTTP.post(`/library/${id}/`, null, formData);
	},
	//上传文件合并
	combineFile(id, fileName) {
		return HTTP.post(`/library/${id}/${fileName}/combine`);
	},
	//修改文件名
	updateFolderName(id, params, formData) {
		return HTTP.put(`/library/${id}`, null, formData);
	},
	//修改文件名称
	updateFileName(id, filename, params, formData) {
		return HTTP.put(`/library/${id}/${filename}`, null, formData);
	},
	//删除文件夹
	deleteFolder(id) {
		return HTTP.delete(`/library/${id}`);
	},
	//删除文件
	deleteFile(id, fileName) {
		return HTTP.delete(`/library/${id}/${fileName}`);
	},
