const state = {
    id:'',
    name:'',
    uploadFolder:false,
    uploadFiles:false,
    libraryTable:false,
    loadding:false,
    updateName:false
}
  
const mutations = {
    getfolderId(state,id){
        // var changeStateName = '';
        state.id = id;
    },
    getfolderName(state,name){
        state.name = name; 
    },
    changeUploadFolder(state,uploadFolder){
        state.uploadFolder = uploadFolder; 
    },
    changeUploadFiles(state,uploadFiles){
        state.uploadFiles = uploadFiles; 
    },
    changelibraryTable(state,libraryTable){
        state.libraryTable = libraryTable; 
    },
    changeloadding(state,loadding){
        state.loadding = loadding; 
    },
    changeupdateName(state,updateName){
        state.updateName = updateName; 
    }
}
  
export default {
    state,
    mutations
}