import Vue from 'vue';

const state = {
    dataset: {
        dataset: null
    }
}

const mutations = {
    // 数据输入
    getWorkflowInfo(state, items) {
        if (!state.dataset.dataset) {
            Vue.set(state.dataset, 'dataset', items);
        }
        state.dataset.dataset = items;
    },

    // 初始化数据
    getDefaultState() {
        state.dataset.dataset = null;
    }
}
export default {
    state,
    mutations
}