// 用于绘制结果图
const state = {
    collects: {
        data: [],
        maxX: 0,
        maxY: 0,
        minX: 0,
        minY: 0,
        xlab: 'xlab',
        ylab: 'ylab',
    }, // 初始化一个collects
}

// get 获取状态
// const getters = {
//     renderCollects(state) {
//         return state.collects
//     }
// }

// 同步更新 状态
const mutations = {
    pushCollects(state, items) {
        state.collects.data = items.data
        state.collects.maxX = items.maxX
        state.collects.maxY = items.maxY
        state.collects.minX = items.minX
        state.collects.minY = items.minY
        state.collects.xlab = items.xlab
        state.collects.ylab = items.ylab
    }
}


// 到处模块
export default {
    // namespaced: true,
    state,
    // getters,
    mutations,
}