// 网站运行追踪

const state = {
  navWorkflow: []
};

const mutations = {

  // 单条数据插入
  postNavWorkflowNode(state, items) {
    state.navWorkflow.push(items);
  },

  // 整个数据覆盖
  postNavWorkflowData(state, items) {
    state.navWorkflow = items;
  },

  // 初始化数据
  getNavWorkflowState() {
    state.navWorkflow = [];
  }
};

export default {
  state,
  mutations
}