<script>
import axios from "axios";

export default {
  name: "home",
  data() {
    return {
      webHeight: 0,
      webWidth: 0,
      i: 1,
      scrollT: 0,
      username: "", // username
      footData: {}
    };
  },
  mounted() {
    // var that = this;
    this.webHeight =
      screen.height -
      (window.outerHeight - window.innerHeight) -
      (screen.height - screen.availHeight) -
      (window.innerHeight - document.body.clientHeight);
    window.addEventListener("scroll", this.getScroll, true);
    this.webWidth = screen.width - (window.outerWidth - window.innerWidth);
    //设置列表的高度自适应窗口
    // let resizeTimer = null;
    // let $searchlist = this.$el.querySelector('.searchlist');
    // let body = document.querySelector('body');
    // window.onresize = () => {
    //   if (resizeTimer) {
    //     clearTimeout(resizeTimer);
    //   }
    //   let bodyStyle = window.getComputedStyle(body);
    //    console.log(bodyStyle.height);
    //   resizeTimer = setTimeout(() => {
    //     $searchlist.style.maxHeight = '';
    //   }, 400);
    // };
    this.requestData();
  },

  watch: {
    scrollT() {
      this.viewScroll();
    }
  },

  destroyed() {
    window.removeEventListener("scroll", this.getScroll);
  },

  methods: {
    //监听滚动
    getScroll() {
      this.scrollT = Math.floor(
        document.body.scrollTop ||
          document.documentElement.scrollTop ||
          window.pageXOffset
      );
    },

    viewScroll() {
      // 页面滚动距顶部距离
      var scrollT = Math.floor(
        document.body.scrollTop ||
          document.documentElement.scrollTop ||
          window.pageXOffset
      );
      let that = this;
      let scroll = scrollT - this.i;
      this.i = scrollT;

      if (scroll < 0) {
        // window.console.log("Up")
      } else {
        // window.console.log("Down")
        if (scrollT < that.webHeight - 20 && scrollT > 35) {
          var timer = setInterval(function () {
            var scrollT =
              document.body.scrollTop || document.documentElement.scrollTop;
            var step = Math.floor(scrollT / 30);
            document.body.scrollTop = document.documentElement.scrollTop =
              step + scrollT;
            if (scrollT >= that.webHeight) {
              document.body.scrollTop = document.documentElement.scrollTop =
                that.webHeight;
              that.i = scrollT;
              clearTimeout(timer);
            }
          }, 10);
        } else if (that.webHeight == scrollT) {
          // window.console.log("false")
          return false;
        }
      }
    },

    startScroll() {
      var that = this;
      let timer = setInterval(function () {
        let scrollT =
          document.body.scrollTop || document.documentElement.scrollTop;
        let step = Math.floor((300 / 3) * 0.2);
        document.body.scrollTop = document.documentElement.scrollTop =
          step + scrollT;
        if (scrollT >= that.webHeight * 2) {
          document.body.scrollTop = document.documentElement.scrollTop =
            that.webHeight * 2;
          clearTimeout(timer);
        }
      }, 10);
    },

    toTool() {
      // 当用户已经登陆成功，且已经记录了，就可以直接
      if (this.$cookies.get("token")) {
        this.$store.commit(
          `handleToken/REFRESH_TOKEN`,
          this.$cookies.get("token")
        );
        sessionStorage.setItem(
          "token",
          JSON.stringify(this.$cookies.get("token"))
        );

        this.$ProjectModels.getUserInfoModels().then(res => {
          this.username = res.data.username;
          this.$progress.finish(); // 进度条完成
          // 储存网站追踪信息
          let objs = {
            title: this.username,
            sign: 1
          };
          this.$store.commit("postNavWorkflowData", [objs]);

          this.$router.push({ path: "/projectSet" });
        });
      } else {
        this.$router.push({
          path: "/Login",
          query: { id: 1 }
        });
      }
    },

    requestData() {
      axios({
        method: "get",
        url: "https://phenomics.fudan.edu.cn/firmiana/DP/v01/portalStatistic",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
        }
      })
        .then(res => {
          this.footData = res.data;
        })
        .catch(err => {
          window.console.log(err);
        });
    }
  }
};
</script>


