// 储存projectId和projectName
const state = {
    proInfo: {
        projectId: null,
        projectName: 'Choose project'
    }
};

const mutations = {
    getProjectId(state, items) {
        state.proInfo.projectId = items.id;
        state.proInfo.projectName = items.name;
    }
};

export default {
    state,
    mutations
};