const state = {
    workflowId:'',
    resultNodeId:"",
}
  
const mutations = {
    getWorkflowId(state,changeWorkflowId){
        // var changeStateName = '';
        state.workflowId = changeWorkflowId;
    },
    getResultNodeId(state,changeResultNodeId){
        // var changeStateName = '';
        state.resultNodeId = changeResultNodeId;
    }
}
  
export default {
    state,
    mutations
}