// 导入封装好的axios实例
import request from "./instance";

const http = {
	/**
	 * methods: 请求
	 * @param url 请求地址
	 * @param params 请求参数
	 */
	get(url, params) {
		const config = {
			method: "get",
			url: url,
		};
		if (params) config.params = params;
		if (params == "download") config.responseType = "blob";
		if (params == "img") config.responseType = "image/png;charset=utf8";
		return request(config);
	},
	post(url, params, formData) {
		const config = {
			method: "post",
			url: url,
		};
		if (params) config.data = params;
		if (formData) config.data = formData;
		return request(config);
	},
	put(url, params, formData) {
		const config = {
			method: "put",
			url: url,
		};
		if (params) config.params = params;
		if (formData) config.data = formData;
		return request(config);
	},
	delete(url, params) {
		const config = {
			method: "delete",
			url: url,
		};
		if (params) config.params = params;
		return request(config);
	},
};
//导出
export default http;
