import Vue from 'vue';
// 节点信息储存

const state = {
    DAG: {
        name:null,
        edges: [],
        nodes:[]
    }
}

const mutations = {
    postWorkflowInfo(state, items) {
        if (!state.DAG.name) {
            Vue.set(state.DAG, 'name', items.name);
        }
        // window.console.log(items);
        state.DAG.name = items.name;
        state.DAG.edges = items.edges;
        state.DAG.nodes = items.nodes;
    }
}
export default {
    state,
    mutations
}