/**
 * axios.js
 */
import {
  message
} from 'ant-design-vue';
import axios from 'axios';
import store from '../store/store';
import router from '../router';
import handle401 from './handle401';

// 创建新的axios实例
var instance = axios.create({
  // 超时时间 单位是ms，这里设置了12s的超时时间
  timeout: 1000 * 100
});
// 添加统一接口
instance.defaults.baseURL = 'https://phenomics.fudan.edu.cn/firmiana/DP/v01';
// 设置统一header
instance.defaults.headers.post['Content-Type'] = 'application/json';

// 设置axios拦截器，请求携带token
instance.interceptors.request.use(config => {
  // 判断是为了防止自定义header被覆盖
  // 判断是否为空token
  store.state.folder.loadding=true
  if (store.state.handleToken.token) {
    // 判断时重新登陆还是重新获取token
    if (config.url == '/refresh') {
      config.headers.common.authorization = 'Bearer ' + store.state.handleToken.refreshToken;
    } else {
      config.headers.common.authorization = 'Bearer ' + store.state.handleToken.token;
    }
  } else if (sessionStorage.getItem('token')) {
    if (config.url == '/refresh') {
      config.headers.common.authorization = 'Bearer ' + JSON.parse(sessionStorage.getItem("token")).refreshToken;
    } else {
      config.headers.common.authorization = 'Bearer ' + JSON.parse(sessionStorage.getItem("token")).token;
    }
  }
  return config;
});


// 若因401而拒绝，则刷新token，若403则跳转登录
// 返回的内容将会替换当前请求（Promise链式调用）

instance.interceptors.response.use(
  (response) => {
    store.state.folder.loadding=false
    /*****  删除成功的提示  *****/
    if (response.status === 204) {
      message.success('Deletion succeeded');
    } else if (response.status === 200) {
      return Promise.resolve(response);
    } else if (response.status === 201) {
      return Promise.resolve(response);
    } else {
      return Promise.reject(response);
    }
    /***** 处理结束 *****/

    //如果token可用，则不需要上面处理
  },
  // 服务器状态码不是200的情况    
  (error) => {
    // 1.公共错误处理
    if (error.response.status) {
      // 2.根据响应码具体处理
      switch (error.response.status) {
        case 400:
          message.error(error.response.data.msg);
          break;

        case 401:
          /*****  对token进行响应，处理token的更新以及重新登陆  *****/
          // 当返回的错误是401时，进行处理。
          if (error.response.status === 401) {
            const {
              config
            } = error;
            // 如果错误为401且url为请求refresh，则需要提醒用户重新登陆。
            if (config.url == '/refresh') {
              message.warn('ID card expired, please log in again');
              router.replace({
                path: '/Login',
                query: {
                  id: 1
                }
              });
            } else {
              // 当返回的错误仅仅是401时，需要更换token，则需要发送refresh token来获取新的token。
              return handle401(config);
            }
          }
          /***** 处理结束 *****/
          break;

        case 403:
          message.error(error.response.data.msg);
          break;
        case 404:
          message.error(error.response.data.msg);
          window.location.href = "/NotFound";
          break;
        case 405:
          message.error(error.response.data.msg);
          break;
        case 408:
          message.error(error.response.data.msg);
          break;
        case 500:
          message.error(error.response.data.msg);
          break;
        case 501:
          message.error(error.response.data.msg);
          break;
        case 502:
          message.error(error.response.data.msg);
          break;
        case 503:
          message.error(error.response.data.msg);
          break;
        case 504:
          message.error(error.response.data.msg);
          break;
        case 505:
          message.error =(error.response.data.msg);
          break;
        default:
          message.error(error.response.data.msg);
      }
    } else {
      // 超时处理
      if (JSON.stringify(error).includes('timeout')) {
        message.error('服务器响应超时，请刷新当前页');
      }
      message.error('连接服务器失败');
    }
    /***** 处理结束 *****/
    //如果不需要错误处理，以上的处理过程都可省略
    return Promise.reject(error.response);
  }
);

export default instance; // 导出instance实例，所有请求都使用这个对象